import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import axios from 'axios';







function Tabss() {

  
    const [locations, setCount] = useState([]);
    useEffect(() => {
      
      axios({
        url:'http://localhost:2029/locations',
        method:'GET',
        headers:{'Content-Type':'application/json'}
    })
    .then(responce=>
        setCount({locations:responce.data.locations})
        
    )
    .catch()
    },[]);
    {console.log(locations)}

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 3 }}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.number.isRequired,
  value: PropTypes.number.isRequired,
};


  function a11yProps(index) {
    return {
      id: `simple-tab-${index}`,
      'aria-controls': `simple-tabpanel-${index}`,
    };
  }

  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };
    return (
      <div>
        
      <Box sx={{ borderBottom: 1, borderColor: 'divider',margin:"100px" }}>
      <Tabs value={value} onChange={handleChange} aria-label="basic tabs example" indicatorColor="secondary"  textColor="secondary">
        <Tab label="Personal Profile" {...a11yProps(0)} />
        <Tab label="Change Password" {...a11yProps(1)} />
        <Tab label="Master List" {...a11yProps(2)} />
      </Tabs>
    </Box>
    <TabPanel value={value} index={0}>
   {/* <div className="container mx-auto">
            {locations.map(d => (
                <h1>{d.name}</h1>
            ))}
            </div> */}
  
    </TabPanel>
    <TabPanel value={value} index={1}>
  
    
          
    </TabPanel>
  
    <TabPanel value={value} index={2}>
      
   
    
   
    </TabPanel>
    
    </div>
    )
    }

export default Tabss;
