import React from 'react';
import '../Styles/Orders.css';
import axios from 'axios';
import queryString from 'query-string';




class Orders extends React.Component{

    constructor(){
        super();
        this.state= {
            Paydetails :[],
            

        }
    }

    componentDidMount(){
        //"this.props.location.search" is syntax to read value from query string & it's constant
   const qs = queryString.parse(this.props.location.search);
   const{account}=qs;

   axios({
    url:`http://localhost:2029/PayDetail/${account}`,
    method:'GET',
    headers:{'Content-Type':'application/json'}
})
.then(responce=>{
    this.setState({Paydetails:responce.data.Paydetails})
})
.catch()

    }

    handleDelete=(resID)=>{    

        
   axios({
    url:`http://localhost:2029/PayDetail/${resID}`,
    method:'DELETE',
    headers:{'Content-Type':'application/json'}
    
})
.then(responce=>{
    window.location.reload(false);
    this.setState({Paydetails:responce.data.Paydetails});
    
})
.catch()
    }

   
    render(){
        const{Paydetails}=this.state;
return(


    <div className="col-xl-9 col-lg-9 col-md-9 col-sm-12 container">
        <h1 className="Orderhead">Orders Placed</h1>

{Paydetails.length>0 ?Paydetails.map((item,index)=>{
                        return <div>
                            <h3>{index+1}.{item.shopName}</h3>
                            <div className="meal1">
                           
                        <span className="ordermeal">{`${item.meal}`}</span> 
                        <br/>
                        
                        <span className="Amount"> {`${item.cost}`}</span>
                        <br/>
                        
                     
                        
                        </div>

                        
                    
                        <div className="Amount">TotalAmount: &#8377;{item.amount}

                       
                         <i class="fas fa-trash" onClick={()=>this.handleDelete(item._id)}></i>
                        
                       
                        </div>
                        <div className="Adddetails">
                            <h4>Address Details</h4>
                         <div>{item.name}</div>
                         <div>{item.mobile}</div>
                         <div>{item.email}</div>
                         <div>{item.address}</div>
                     </div>
                       
                      <hr/>
                        </div>
                         
                        
                    }):<div className=" NoData">No orders Placed...</div>}

  
            
    
</div>

   )

}}
export default Orders;
